// interceptors/auth.interceptor.ts
import { Injectable } from '@angular/core';  // Importation du d√©corateur Injectable pour pouvoir injecter ce service dans d'autres parties de l'application
import { HttpInterceptor, HttpRequest, HttpHandler, HttpEvent } from '@angular/common/http';  // Importation des classes n√©cessaires pour intercepter les requ√™tes HTTP
import { Observable } from 'rxjs';  // Importation d'Observable pour g√©rer les flux asynchrones
import { AuthService } from '../services/auth/auth.service';  // Importation du service AuthService
import { catchError } from 'rxjs/operators';  // Importation de l'op√©rateur catchError pour g√©rer les erreurs
import { throwError } from 'rxjs';  // Importation de la fonction throwError pour g√©rer les erreurs
import { Router } from '@angular/router';
import { EMPTY } from 'rxjs';

@Injectable()  // Ce d√©corateur permet d'injecter ce service dans toute l'application
export class AuthInterceptor implements HttpInterceptor {  // Impl√©mentation de l'interface HttpInterceptor pour intercepter les requ√™tes HTTP sortantes
  constructor(private authService: AuthService, private router: Router) {}

  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {  // La m√©thode intercept prend une requ√™te HTTP et un gestionnaire de requ√™te
    const token = localStorage.getItem('token');
    
    // Ne pas intercepter les requ√™tes d'authentification
    if (request.url.includes('/account/signin') || request.url.includes('/account/google')) {
      return next.handle(request);
    }

    if (token) {
      request = request.clone({
        setHeaders: {
          Authorization: `Bearer ${token}`
        }
      });
    } else {
      // Si pas de token et qu'on n'est pas sur une route publique, rediriger vers login
      if (!this.isPublicRoute(request.url)) {
        this.router.navigate(['/login']);
        return EMPTY;
      }
    }

    return next.handle(request).pipe(
      catchError(error => {
        if (error.status === 401) {
          this.authService.logout();
          this.router.navigate(['/login']);
        }
        return throwError(() => error);
      })
    );
  }

  private isPublicRoute(url: string): boolean {
    const publicRoutes = [
      '/login',
      '/register',
      '/products', // Route publique pour voir les produits
      '/home'
    ];
    return publicRoutes.some(route => url.includes(route));
  }
}
