// Importation des modules n√©cessaires
import { Injectable } from '@angular/core'; // Injectable est un d√©corateur qui permet d'utiliser cette classe comme service dans Angular
import { CanActivate, Router } from '@angular/router'; // CanActivate est un garde de s√©curit√© pour prot√©ger les routes, Router est utilis√© pour naviguer entre les routes
import { AuthService } from '../../../services/auth/auth.service'; // Service d'authentification, probablement utilis√© pour v√©rifier l'√©tat de l'utilisateur

// D√©claration du service AdminGuard comme injectable dans l'application
@Injectable({
  providedIn: 'root' // Ce service est disponible globalement, il est inject√© automatiquement dans toute l'application
})
export class AdminGuard implements CanActivate {
  constructor(
    private authService: AuthService, // Injection du service d'authentification
    private router: Router // Injection du service de navigation pour rediriger l'utilisateur
  ) {}

  // Impl√©mentation de la m√©thode canActivate pour v√©rifier l'acc√®s avant d'autoriser la navigation
  canActivate(): boolean {
    // Affichage dans la console pour le d√©bogage, indiquant qu'on est en train de v√©rifier les droits d'administrateur
    console.log('AdminGuard - Checking admin rights');
    
    // Affichage dans la console si l'utilisateur est un administrateur
    console.log('Is Admin?', this.authService.isAdmin());
    
    // Affichage des informations de l'utilisateur actuel pour le d√©bogage
    console.log('Current User:', this.authService.currentUser$);

    // Si l'utilisateur n'est pas un administrateur, on refuse l'acc√®s et on redirige vers la page "unauthorized"
    if (!this.authService.isAdmin()) {
      console.log('Access denied - redirecting to unauthorized'); // Affichage dans la console lors du refus d'acc√®s
      this.router.navigate(['/unauthorized']); // Redirection vers la page d'acc√®s non autoris√©
      return false; // Retourne false pour bloquer l'acc√®s √† la route prot√©g√©e
    }

    // Si l'utilisateur est administrateur, l'acc√®s est autoris√©
    console.log('Admin access granted'); // Affichage dans la console pour confirmer l'acc√®s autoris√©
    return true; // Retourne true pour permettre l'acc√®s √† la route prot√©g√©e
  }
}
