import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import {AuthService} from '../../../services/auth/auth.service';


@Injectable({
  providedIn: 'root'
})
export class NoAuthGuard implements CanActivate {
  constructor(
    private authService: AuthService,
    private router: Router
  ) {}

  canActivate(): boolean {
    // V√©rifie si l'utilisateur est d√©j√† connect√©
    if (this.authService.isAuthenticated()) {
      // Si c'est un admin, redirection vers le dashboard admin
      if (this.authService.isAdmin()) {
        this.router.navigate(['/admin/dashboard']);
      } else {
        // Si c'est un client, redirection vers la page des produits
        this.router.navigate(['/client/products']);
      }
      return false; // Emp√™che l'acc√®s √† la route demand√©e
    }
    return true; // Permet l'acc√®s si non connect√©
  }
}
