// register.component.ts

import { Component } from '@angular/core'; // Importation du d√©corateur Component
import { CommonModule, NgOptimizedImage } from '@angular/common'; // Importation des modules communs et d'optimisation d'images
import { FormsModule } from '@angular/forms'; // Importation du module pour la gestion des formulaires
import { Router, RouterModule } from '@angular/router'; // Importation du module de routage
import { AuthService } from '../../services/auth/auth.service'; // Importation du service d'authentification

@Component({
  selector: 'app-register', // D√©finition du s√©lecteur du composant
  standalone: true, // Indique que le composant peut √™tre utilis√© ind√©pendamment
  imports: [CommonModule, FormsModule, RouterModule, NgOptimizedImage], // Modules import√©s n√©cessaires au composant
  templateUrl: './register.component.html', // Chemin du fichier HTML du composant
  styleUrls: ['./register.component.scss'] // Chemin du fichier SCSS du composant
})
export class RegisterComponent {
  // D√©finition de l'objet utilisateur pour stocker les donn√©es du formulaire
  user = {
    username: '',
    mail: '',
    password: '',
    confirmPassword: '',
    firstName: '',
    lastName: '',
    address: ''
  };

  error = ''; // Variable pour stocker les messages d'erreur
  isLoading = false; // Indicateur de chargement

  constructor(
    private authService: AuthService, // Injection du service d'authentification
    private router: Router // Injection du service de navigation
  ) {}

  onSubmit() {
    // V√©rification si les mots de passe correspondent
    if (this.user.password !== this.user.confirmPassword) {
      this.error = 'Les mots de passe ne correspondent pas'; // Message d'erreur si les mots de passe sont diff√©rents
      return;
    }

    // Cr√©ation de l'objet √† envoyer √† l'API pour l'inscription
    const signupPayload = {
      username: this.user.username,
      mail: this.user.mail,
      password: this.user.password,
      googleHash: '', // Champs pour √©ventuelles connexions via Google/Facebook (non utilis√©s ici)
      facebookHash: ''
    };

    // Appel du service d'inscription avec l'objet signupPayload
    this.authService.signup(signupPayload).subscribe({
      next: (response) => {
        console.log('Inscription r√©ussie:', response); // Log en cas de succ√®s
        this.router.navigate(['/login']); // Redirection vers la page de connexion apr√®s succ√®s
      },
      error: (error) => {
        console.error('Erreur inscription:', error); // Log en cas d'erreur
        // Gestion de l'affichage du message d'erreur en fonction du statut HTTP retourn√©
        this.error = error.status === 409
          ? 'Cet email est d√©j√† utilis√©. Veuillez en choisir un autre ou vous connecter.'
          : 'Erreur lors de l\'inscription';
      }
    });
  }
}
